.elements {
  display: grid;
  margin: 45px 0 0;
  gap: 20px 17px;
  grid-template-columns: repeat(auto-fit, 282px);
  grid-template-rows: auto;
  padding: 0;
  justify-items: center;
  justify-content: center;
}

@media screen and (max-width: 680px) {
  .elements {
    padding: 0 19px;
  }
}
